.footer {
    background-color: #333;
    color: white;
    padding: 1rem;
    text-align: center;
  }
  
  .nav {
    display: flex;
    justify-content: center;
  }
  
  .nav-list {
    list-style: none;
    display: flex;
    gap: 1rem;
    padding: 0;
  }
  
  .nav-item {
    font-size: 18px;
  }
  
  .nav-link {
    text-decoration: none;
    color: white;
    transition: color 0.3s ease-in-out;
  }
  
  .nav-link:hover {
    color: lightgray;
  }
  
  .powered-by {
    margin-top: 1rem;
  }
  
  .powered-by-link {
    text-decoration: none;
    color: white;
    font-weight: bold;
  }
  
  .powered-by-link:hover {
    color: lightgray;
  }